        "不，艰难的道路请让我来探寻，你只需要眺望远方，鹰会传来我新的功绩，而四方的人也将传诵你的名字。",
        "不，我们需要一个魔王来成就英雄",
        "不论如何，我还是一个骑士，虽然口袋里没有一个铜板。",
        "成为新的英雄？你？我想你或许会在故事的第二十八页就死掉。",
        "但没有人知道他们之后去了哪里，如一场浩浩荡荡的长跑，人们只会记住最后在终点看到的人。",
        "当时代的光芒被浓缩成发黄纸页上的寥寥数字。真实，也不过是永恒面前一个短小的笑话。",
        "当我成为一个英勇的骑士，我将要求所有败在我剑下的人承认云迪是世上最美的女子，并传颂她的名字。",
        "康德，你所相信的，你会一直这样坚持吗？",
        "康德，如果你要去历险，请也通知我好吗？我愿意一路与你同行。",
        "魔王们做过许多事情，比如在大地上建了许多堵墙，并不是为了守护他的领地，只为了拼出一个单词让天主看到，当那句太空中能看到的唯一脏话印在辽阔的大地上，众神一定会后悔他们有那么遥远的眼界。",
        "你还太年轻，你不明白有些东西并不是仅靠努力就能得到的……当你想得到一样东西却又感觉自己无力，你就会倒向黑暗……",
        "你活在真实的世界里，所以未来未必注定。",
        "你说，这世上为什么就没有一本以我们魔族为主角的书：我们打败了可恶的骑士，把教皇封印了起来，然后和公主一起过着幸福安宁的生活……",
        "诺言是不能信的，未来是不注定的。康德，告诉我，有什么是可以依靠的呢？",
        "人生不过是匆匆划过天空的一支箭，而无数人奔向他们终点的历程，由此汇成了辉煌壮观的奇景。",
        "人生不过是周而复始，未来总是虚不可测，你不知道下一刻会不会来。",
        "如果神不给于众生他们的怜悯，那就让他们自己去怜悯自己吧！",
        "世间上真的有天主吗？为什么我会遭遇这样的命运？我从未想过背叛光明与道德……",
        "死人也能做圣骑士，断腿又有什么关系？",
        "他看见那个女孩从门口走进来，像星辰忽然降临在人间……",
        "他们就从来不曾看到一个人破旧的外套下那高傲而正直的'心吗？哦，见鬼，这我自己都觉得没用……",
        "太阳落下前一瞬，人们浑然不知，它将不再升起。",
        "未来本来就是希望消亡的过程，一切都将展现在你的眼前，再不会有悬念，不会有盼望，你以为你所能追寻到的一切，最后都消失在遥边的天际。",
        "未来就是巨轮的重新转动，是大地将在你们面前开启。你们会看见天空，看见你们一生以来从未见过的天空。",
        "未来是古岩石上滴下的一滴水，三千年后才能到达地心。我们世代居住在这黑暗巨穹之下，时间是没有意义的。",
        "我认为人应该去做点儿什么，而不是在闲聊与牌戏中度过一生。这个世界上的某处，也许就在那座山的背后，一定有我们所不知道的奇迹存在。",
        "宿命如黑洞，吸走一切可能之光； 唯有英雄背负腐朽，孤独前行。",
        "一场浩浩荡荡的长路，人们只会记住最后在终点看到的人",
        "一个骑士不会放弃他的高贵使命而活下去！",
        "这个世上有你所需要的一切，只要你愿意掏空你的口袋。",
      ],
      lines: ""
    }
  },
  created() {
    this.createLines();
  },
  methods: {
    /**
     * 随机获取 x~y 之间的一个整数，其中 x 和 y 都是整数。
     * 公式: Math.round(Math.random()*(y-x)+x)
     */
    createLines() {
      const Len = this.list.length, Index = Math.round(Math.random() * (Len - 1));
      this.lines = this.list[Index];
    }
  }
}
</script>
<style lang="less" scoped>
.footer {
  color: @silvery-l;
}
</style>