<template>
  <div class="header">
    <div class="nav glass" ref="nav">
      <router-link
        class="nav-item"
        v-for="(v, i) in menus"
        :key="i"
        :to="v.path"
        >{{ v.label }}</router-link
      >
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      menus: [],
      timer: null,
    };
  },
  mounted() {
    this.menus = [];
    const routes = this.$router.options.routes;
    routes.forEach((v) => {
      if (v.meta?.title)
        this.menus.push({
          label: v?.meta.title,
          path: v.path,
        });
    });
    this.demo = 
    document.addEventListener("mousemove", (e) => {
      this.toggle(e)
    })
  },
  methods: {
    toggle(e) {
      this.timer && clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        if(e.y <= 160) {
          this.$refs["nav"].classList.add("nav-hover")
        } else {
          this.$refs["nav"].classList.remove("nav-hover")
        }
      }, 50)
    }
  }
};
</script>
<style lang="less" scoped>
.header {
  height: 80px;
  position: relative;
  z-index: 99;
}
.nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 40px;
  // background: #ffffff;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.1);

  display: flex;
  justify-content: center;

  transition: all .3s;
  .nav-item {
    margin: auto 20px;
  }
  .router-link-active {
    
  }
}
.nav-hover {
  height: 80px;
  background: #FFFFFF;
}
</style>